.InputBox-module__inputWrapper_adOgOW__0261 {
  pointer-events: all;
  align-items: stretch;
  gap: 6px;
  display: flex;
}

.InputBox-module__chatInput_adOgOW__0261 {
  color: #fff;
  backdrop-filter: blur(10px);
  box-sizing: border-box;
  background: #0006;
  border: 1px solid #fff3;
  border-radius: 8px;
  outline: none;
  flex: 1;
  height: 36px;
  padding: 0 8px;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen, Ubuntu, Cantarell, sans-serif;
  font-size: 14px;
  font-weight: 400;
  transition: all .2s ease-in-out;
}

.InputBox-module__chatInput_adOgOW__0261:focus {
  background: #0009;
  border-color: #fff6;
  box-shadow: 0 0 0 3px #ffffff26;
}

.InputBox-module__chatInput_adOgOW__0261::placeholder {
  color: #ffffff80;
}

.InputBox-module__sendButton_adOgOW__0261 {
  color: #ffffffe6;
  backdrop-filter: blur(10px);
  cursor: pointer;
  background: linear-gradient(135deg, #0009, #0006);
  border: 1px solid #fff3;
  border-radius: 8px;
  outline: none;
  justify-content: center;
  align-items: center;
  width: 36px;
  height: 36px;
  transition: all .2s ease-in-out;
  display: flex;
}

.InputBox-module__sendButton_adOgOW__0261:hover {
  background: linear-gradient(135deg, #000c, #0009);
  border-color: #ffffff4d;
  transform: translateY(-1px);
  box-shadow: 0 4px 8px #0000004d;
}

.InputBox-module__sendButton_adOgOW__0261:active {
  transform: translateY(0);
  box-shadow: 0 2px 4px #0003;
}

.InputBox-module__sendButton_adOgOW__0261 .InputBox-module__svgIcon_adOgOW__0261 img {
  filter: invert(80%);
  width: 18px;
  height: 18px;
  transition: all .2s ease-in-out;
}

.InputBox-module__sendButton_adOgOW__0261:hover .InputBox-module__svgIcon_adOgOW__0261 img {
  filter: invert(90%);
}
